/* ========================================================================
 * DOM-based Routing
 * Based on http://goo.gl/EUTi53 by Paul Irish
 *
 * Only fires on body classes that match. If a body class contains a dash,
 * replace the dash with an underscore when adding it to the object below.
 *
 * .noConflict()
 * The routing is enclosed within an anonymous function so that you can
 * always reference jQuery with $, even when in .noConflict() mode.
 * ======================================================================== */

(function($) {

	// Use this variable to set up the common and page specific functions. If you
	// rename this variable, you will also need to rename the namespace below.
	var Sage = {
		// All pages
		'encuesta': {
			init: function () {
				$( 'span[data-toggle="collapse"]' ).hover(
					function() {
						console.log('asdas');
						$('.collapse').collapse('hide');
						$(this).next('.collapse').collapse('show');
					}, function() {
						$('.collapse').collapse('hide');
					}
				);
				$( 'span[data-toggle="collapse"]' ).on('click', function (e) {
					$('.collapse').collapse('hide');
					$(this).next('.collapse').collapse('show');
				});
			}
		},
		'result_test': {
			init: function () {
				var data = [
					{
						label: "Otros",
						data: 56
					},
					{
						label: "Si",
						data: 44
					}
				];
				var labelFormatter = function(label, series) {
					return "<div style='font-size:8pt; text-align:center; padding:2px; color:white;'>" + label + "<br/>" + Math.round(series.percent) + "%</div>";
				};
				$.plot('#ResultsChart', data, {
					series: {
						pie: {
							show: true,
							radius: 1,
							label: {
								show: true,
								radius: 2/3,
								formatter: labelFormatter,
								threshold: 0.1
							}
						}
					},
					legend: {
						show: false
					}
				});
			}
		},
		'common': {
			init: function() {
				window.cargarUrl = function ( url ) {
					window.location.assign( url );
				};
				// JavaScript to be fired on all pages
				$('#list_carreras').DataTable( {
					"dom": "<'row'<'col-sm-12'l><'col-sm-12'f>>" +
					"<'row'<'col-sm-24'tr>>" +
					"<'row'<'col-sm-10'i><'col-sm-14'p>>",
					"language": {
						"sProcessing":	 "Procesando...",
						"sLengthMenu":	 "Mostrar _MENU_ registros",
						"sZeroRecords":	"No se encontraron resultados",
						"sEmptyTable":	 "Ningn dato disponible en esta tabla",
						"sInfo":			 "Mostrando registros del _START_ al _END_ de un total de _TOTAL_ registros",
						"sInfoEmpty":		"Mostrando registros del 0 al 0 de un total de 0 registros",
						"sInfoFiltered":	 "(filtrado de un total de _MAX_ registros)",
						"sInfoPostFix":	"",
						"sSearch":		 "Buscar:",
						"sUrl":			"",
						"sInfoThousands":	",",
						"sLoadingRecords": "Cargando...",
						"oPaginate": {
							"sFirst":	"Primero",
							"sLast":	 "ltimo",
							"sNext":	 "Siguiente",
							"sPrevious": "Anterior"
						},
						"oAria": {
							"sSortAscending":	": Activar para ordenar la columna de manera ascendente",
							"sSortDescending": ": Activar para ordenar la columna de manera descendente"
						}
					}
				} );

					$('[data-toggle="tooltip"]').tooltip();
					$('.input-group.date').datepicker({
					format: "dd/mm/yyyy",
					endDate: "date()",
					language: "es",
					keyboardNavigation: false,
					autoclose: true,
					todayHighlight: true
				});
			},
			finalize: function() {
				// JavaScript to be fired on all pages, after page specific JS is fired
			}
		},
		// Home page
		'home': {
			init: function() {
				// JavaScript to be fired on the home page
			},
			finalize: function() {
				// JavaScript to be fired on the home page, after the init JS
			}
		},
		// About us page, note the change from about-us to about_us.
		'about_us': {
			init: function() {
				// JavaScript to be fired on the about us page
			}
		}
	};

	// The routing fires all common scripts, followed by the page specific scripts.
	// Add additional events for more control over timing e.g. a finalize event
	var UTIL = {
		fire: function(func, funcname, args) {
			var fire;
			var namespace = Sage;
			funcname = (funcname === undefined) ? 'init' : funcname;
			fire = func !== '';
			fire = fire && namespace[func];
			fire = fire && typeof namespace[func][funcname] === 'function';

			if (fire) {
				namespace[func][funcname](args);
			}
		},
		loadEvents: function() {
			// Fire common init JS
			UTIL.fire('common');

			// Fire page-specific init JS, and then finalize JS
			$.each(document.body.className.replace(/-/g, '_').split(/\s+/), function(i, classnm) {
				UTIL.fire(classnm);
				UTIL.fire(classnm, 'finalize');
			});

			// Fire common finalize JS
			UTIL.fire('common', 'finalize');
		}
	};

	// Load Events
	$(document).ready(UTIL.loadEvents);

})(jQuery); // Fully reference jQuery after this point.
